import { addArticles } from "@/api/article"
export default {
  middleware: 'auth',
  name: 'editorinedx',
  data(){
    return{
      article :{
        title :"",
        description :"",
        body :"",
        tagList :[]
      },
      tagText:""
    }
  },
  methods:{
    onkeyup(){
      console.log(this.tagText)
      this.article.tagList.push(this.tagText);
      this.tagText="";
    },
    delTag(index){
      this.article.tagList.splice(index,1)
    },
    async onSubmit(){
      console.log(123)
      const data=await addArticles({
        article :this.article 
      })
      this.$router.push("/")
    }
  }
}
